import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-topics',
  templateUrl: './topics.component.html',
  styleUrls: ['./topics.component.css']
})
export class TopicsComponent implements OnInit {

  current = 1;

  constructor() { }

  ngOnInit() {
    setInterval( () => {
      this.current >= 11 ? this.current = 1 : this.current ++;
    }, 3000);
  }

}
