import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HomeComponent } from './components/home/home.component';
import { Routes, RouterModule } from '@angular/router';
import { HeaderComponent } from './components/header/header.component';
import { BodyComponent } from './components/body/body.component';
import { ColumnComponent } from './components/column/column.component';
import { FooterComponent } from './components/footer/footer.component';
import { TopicsComponent } from './components/topics/topics.component';
import { SharedModule } from '../shared/shared.module';
import { MaterialModule } from '../material.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ContactoComponent } from './components/contacto/contacto.component';
import { ElLugarComponent } from './components/el-lugar/el-lugar.component';

const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'el-lugar', component: ElLugarComponent },
  { path: 'contacto', component: ContactoComponent }
];


@NgModule({
  declarations: [HomeComponent, HeaderComponent, BodyComponent, ColumnComponent,
     FooterComponent, TopicsComponent, ContactoComponent, ElLugarComponent],
  imports: [
    RouterModule.forChild(routes),
    CommonModule,
    SharedModule,
    MaterialModule,
    FormsModule,
    ReactiveFormsModule,
  ]
})
export class HomeModule { }
